import React from "react";
import SocialIcons from "./third-party/social-icons/SocialIcons";
import { Card, TextField, Fab, Snackbar, SnackbarContent, MuiThemeProvider, createMuiTheme } from "@material-ui/core/";
import SimpleMap from "./third-party/google/Map";
import "../styles/Contact.css";

const theme = createMuiTheme({
	palette: {
		primary: {
			main: "#0194f6"
		}
	}
});

class Contact extends React.Component {
	state = {
		name: "",
		email: "",
		message: "",
		open: {
			success: false,
			error: false,
			vertical: "bottom",
			horizontal: "right"
		}
	};

	componentDidMount() {
		window.scroll(0, 0);
		document.title = "Chupt - Contact"
	}

	handleNameChange = e => {
		this.setState({ name: e.target.value });
	};

	handleEmailChange = e => {
		this.setState({ email: e.target.value });
	};

	handleMessageChange = e => {
		this.setState({ message: e.target.value });
	};

	handleClose = x => {
		console.log(x);
		if (x === "error") {
			let state = this.state;
			state.open.error = false;
			this.setState(state);
		} else {
			let state = this.state;
			state.open.success = false;
			this.setState(state);
		}
	};

	handleSubmit = e => {
		e.preventDefault();

		let myForm = document.getElementById("email-form");

		let formData = new FormData(myForm);

		fetch("https://chupt-dev-4.appspot.com/contactus/", {
			method: "POST",
			mod: "no-cors",
			body: formData
		}).then(res => {
			let newState = this.state.open;
			if (res.ok) {
				newState.success = true;

				this.setState({
					name: "",
					email: "",
					message: "",
					open: newState
				});
			} else {
				newState.error = true;

				this.setState({
					open: newState
				});
			}
		});
	};

	render() {
		const { success, error, vertical, horizontal } = this.state.open;

		return (
			<MuiThemeProvider theme={theme}>

				<div className="contact-wrapper">
					<div id="our-address" className="phantomDiv" />

					<Card className="card">
						<SimpleMap />

						<div className="spacer-md_lg" />

						<div className="inner-contact">
							<div className="contact-text">
								{/* <h2>737 SW 11th Ave, Portland, OR 97205</h2> */}

								<div className="inner-card hours">
									<img src={require("../assets/contact/baseline-access_time-24px.svg")} />
									<p className="text">Monday - Friday</p>
									<p className="text">11AM - 3PM</p>
									<div className="phone spacer-md" />
								</div>

								<div className="inner-card time">
									<img src={require("../assets/contact/baseline-phone-24px.svg")} />
									<p className="phone-number text">(123) 456-7890</p>
									<div className="phone spacer-md" />
								</div>

								<div className="inner-card email">
									<img src={require("../assets/contact/baseline-mail_outline-24px.svg")} />
										<p className="text">info@chupt.com</p>
								</div>
							</div>
							<span className="social-icons-wrapper">
								<div className="spacer-md_lg" />
								<SocialIcons />
							</span>
							<div className="spacer-md_lg" />
							<div id="contact-us" className="contact-form">
								<form id="email-form" onSubmit={this.handleSubmit}>
									<h3 className="form-text title-md">Have questions or comments?</h3>
									<TextField
										required
										id="outlined-required"
										label="Name"
										margin="normal"
										variant="outlined"
										name="name"
										value={this.state.name}
										fullWidth
										onChange={event => this.handleNameChange(event)}
									/>
									<br />
									<TextField
										required
										id="outlined-email-input"
										label="Email"
										className="email-input"
										type="email"
										name="email"
										value={this.state.email}
										autoComplete="email"
										margin="normal"
										variant="outlined"
										onChange={e => this.handleEmailChange(e)}
										fullWidth
									/>
									<br />
									<TextField
										required
										id="outlined-multiline-flexible"
										label="Message"
										name="message"
										multiline
										rows="4"
										value={this.state.message}
										// onChange={this.handleChange('multiline')}
										// className={classes.textField}
										margin="normal"
										variant="outlined"
										fullWidth
										value={this.state.message}
										onChange={e => this.handleMessageChange(e)}
									/>
									<br />
									<span className="email-submit">
										<Fab type="submit" disabled="true" variant="extended" color="primary" size="medium">
											<p style={{ margin: "auto 30pt", fontSize: "14pt" }}>Send</p>
										</Fab>
									</span>
								</form>
							</div>
						</div>

						<div className="snackbar-wrapper">
							<Snackbar
								anchorOrigin={{ vertical, horizontal }}
								open={success}
								autoHideDuration={6000}
								onClose={() => this.handleClose("success")}
							>
								<SnackbarContent
									style={{ backgroundColor: "#0194f6" }}
									message={<span id="message-id">Message Sent. Thank you!</span>}
								/>
							</Snackbar>

							<Snackbar
								anchorOrigin={{ vertical, horizontal }}
								open={error}
								autoHideDuration={6000}
								onClose={() => this.handleClose("error")}
							>
								<SnackbarContent
									style={{ backgroundColor: "#f6585f" }}
									message={<span id="message-id">Message Not Received. Try Again. </span>}
								/>
							</Snackbar>
						</div>
					</Card>
				</div>
			</MuiThemeProvider>
		);
	}
}

export default Contact;
