import React from 'react';
import PropTypes from 'prop-types';
import Button from '@material-ui/core/Button';
import ClickAwayListener from '@material-ui/core/ClickAwayListener';
import Grow from '@material-ui/core/Grow';
import Paper from '@material-ui/core/Paper';
import Popper from '@material-ui/core/Popper';
import MenuItem from '@material-ui/core/MenuItem';
import MenuList from '@material-ui/core/MenuList';
import { withStyles } from '@material-ui/core/styles';

import { NavLink } from "react-router-dom";

import './Menu.css'

const styles = theme => ({
  root: {
    display: 'flex',
  },
  paper: {
    marginRight: theme.spacing.unit * 2,
  },
});

class MenuListComposition extends React.Component {
  state = {
    open: false,
  };

  handleToggle = () => {
    this.setState(state => ({ open: !state.open }));
  };

  handleClose = event => {
    if (this.anchorEl.contains(event.target)) {
      return;
    }

    this.setState({ open: false });
  };

  render() {
    const { classes } = this.props;
    const { open } = this.state;

    return (
      <div id="jsMenu" className={classes.root}>
        <div>
          <Button
            buttonRef={node => {
              this.anchorEl = node;
            }}
            aria-owns={open ? 'menu-list-grow' : undefined}
            aria-haspopup="true"
            onClick={this.handleToggle}
          >
            <img id="menuButton" alt="menuButton" src={require("../../../assets/Hamburger_icon.svg")} />

          </Button>

          <Popper open={open} anchorEl={this.anchorEl} transition disablePortal>
            {({ TransitionProps, placement }) => (
              <Grow
                {...TransitionProps}
                id="menu-list-grow"
                style={{ transformOrigin: placement === 'bottom' ? 'center top' : 'center bottom' }}
              >
                <Paper>
                  <ClickAwayListener onClickAway={this.handleClose}>
                    <MenuList>
                      <NavLink to="/"><MenuItem onClick={this.handleClose}>Home</MenuItem></NavLink>
                      <NavLink to="/menu"><MenuItem onClick={this.handleClose}>Menu</MenuItem></NavLink>
                      <NavLink to="/platform"><MenuItem onClick={this.handleClose}>Platform</MenuItem></NavLink>
                      <NavLink to="/about"><MenuItem onClick={this.handleClose}>About</MenuItem></NavLink>
                      <NavLink to="/contact"><MenuItem onClick={this.handleClose}>Contact</MenuItem></NavLink>
                      
                      {/* <NavLink to="/cart"><MenuItem onClick={this.handleClose}>Cart</MenuItem></NavLink> */}
                    </MenuList>
                  </ClickAwayListener>
                </Paper>
              </Grow>
            )}
          </Popper>
        </div>
      </div>
    );
  }
}

MenuListComposition.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(MenuListComposition);
