import React from 'react';
import PropTypes from 'prop-types';
import ReactDOM from 'react-dom';
import { withStyles } from '@material-ui/core/styles';
import Input from '@material-ui/core/Input';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import FilledInput from '@material-ui/core/FilledInput';
import InputLabel from '@material-ui/core/InputLabel';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import NativeSelect from '@material-ui/core/NativeSelect';

const styles = theme => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  formControl: {
    margin: theme.spacing.unit,
    minWidth: 120,
  },
  selectEmpty: {
    marginTop: theme.spacing.unit * 2,
  },
});

class NativeSelects extends React.Component {
  constructor(props) {
    super(props);
    this.state = {value: this.props.menu.sides[0].item_id};

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
}

  componentDidMount() {
    // this.setState({
    //   labelWidth: ReactDOM.findDOMNode(this.InputLabelRef).offsetWidth,
    // });

    console.log(this.props.menu.sides[0].item_id)
  }

  // handleChange = name => event => {
  //   this.setState({ [name]: event.target.value });
  // };

  handleChange(event) {
    this.setState({value: event.target.value});
    // console.log(event.target.value)
    this.handleSubmit() // Actually add button with onClick(this.handleSubmit)
  }

  handleSubmit(event) {
      
    let sideList = [];

    this.props.menu.sides.map(side => {
        if (side.item_id === this.state.value) {
            return sideList.push(side)
        }
    });

    this.props.changeSide(sideList[0])

  }

  render() {
    const { classes } = this.props;

    let options = [];
    this.props.menu.sides.map(
        side => {
            return (
            options.push(
                <option value={side.item_id}>{side.name}</option>
            )
        )}
    )

    return (
      <div className={classes.root}>

        {/* <button onClick={() => console.log(this.state)}>State</button> */}

        <FormControl className={classes.formControl}>
          <InputLabel htmlFor="age-native-simple">Swap Sides</InputLabel>
          <Select
            native
            value={this.state.value}
            onChange={this.handleChange}
            inputProps={{
              name: 'age',
              id: 'age-native-simple',
            }}
          >
            {options}
          </Select>
        </FormControl>
      </div>
    );
  }
}

NativeSelects.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(NativeSelects);