import React from "react";
import { Card, Button, Fab } from "@material-ui/core/";

import CartItem from './CartItem'

import '../styles/Cart.css'

const style = {
    card: {
        borderRadius: "24px",
        boxShadow: "0 0 0 0",
        marginBottom: "24px",
        padding: "3px"
    }
};

class Cart extends React.Component {

    componentWillMount() {
        this.setState({cart: this.props.cart})
        window.scroll(0, 0);
    }   

    componentDidMount() {
        this.handleAmount()
        this.props.handleCartButtons()
    }

    componentWillReceiveProps() {
        console.log("cartProps")
    }

    handleCountChange = (id, number, type) => {
        let newCart = this.state.cart;

        if (type === 'meal') {
            newCart.meals.forEach(
                item => (item.id === id ? item.count = number : null)
            )
        }

        if (type === 'entree') {
            newCart.entrees.forEach(
                item => (item.id === id ? item.count = number : null)
            )
        }

        if (type === 'side') {
            newCart.sides.forEach(
                item => (item.id === id ? item.count = number : null)
            )
        }

        if (type === 'drink') {
            newCart.drinks.forEach(
                item => (item.id === id ? item.count = number : null)
            )
        }
        
        this.setState({cart: newCart})
        this.handleAmount()
    }

    handleRemove = (id, type) => {

        let newState = this.state;

        if (type === 'meal') {
            newState.cart.meals = newState.cart.meals.filter(x => x.id !== id)
        }

        if (type === 'entree') {
            newState.cart.entrees = newState.cart.entrees.filter(x => x.id !== id)
        }

        if (type === 'side') {
            newState.cart.sides = newState.cart.sides.filter(x => x.id !== id)
        }

        if (type === 'drink') {
            newState.cart.drinks = newState.cart.drinks.filter(x => x.id !== id)
        }

        this.setState(newState)

        this.handleAmount()
    }

    handleAmount = () => {
        this.props.handleCartAmount(this.state.cart.amount)
    }

    render() {

        let mealList = [];
        let entreeList = [];
        let sideList = [];
        let drinkList = [];

        let cart = this.state.cart
        
        cart.meals.forEach(
            x => mealList.push(
                <CartItem id={x.id} item={x.item} count={x.count} handleCountChange={this.handleCountChange} handleRemove={this.handleRemove}/>
            )
        )
        cart.entrees.forEach(
            x => entreeList.push(
                <CartItem id={x.id} item={x.item} count={x.count} handleCountChange={this.handleCountChange} handleRemove={this.handleRemove} />
            )
        )
        cart.sides.forEach(
            x => sideList.push(
                <CartItem id={x.id} item={x.item} count={x.count} handleCountChange={this.handleCountChange} handleRemove={this.handleRemove} />
            )
        )
        cart.drinks.forEach(
            x => drinkList.push(
                <CartItem id={x.id} item={x.item} count={x.count} handleCountChange={this.handleCountChange} handleRemove={this.handleRemove} />
            )
        )

        let header;

        if (mealList.length > 0 || entreeList.length > 0 || sideList.length > 0 || drinkList.length > 0) {
            header = <h1 className="title-md">Cart</h1>
        } else {
            header = <h1 className="title-md">Cart Is Empty</h1>
        }
        
        return (
            <div className="cart-wrapper">

                {/* <h1>Cart Coming Soon...</h1> */}

                <Card className="cart-card-wrapper" style={style.card}>

                    {/* <button onClick={() => console.log(this.state)}>State</button> */}

                    

                    {header}

                    {(mealList.length > 0 ? <h2 className="cart-meals" >Meals</h2> : null)}
                    {mealList}

                    {(entreeList.length > 0 ? <h2 className="cart-entrees" >Entrees</h2> : null)}
                    {entreeList}

                    {(sideList.length > 0 ? <h2 className="cart-sides" >Sides</h2> : null)}
                    {sideList}

                    {(drinkList.length > 0 ? <h2 className="cart-drinks" >Drinks</h2> : null)}
                    {drinkList}
                    
                </Card>
            </div>
        )
    }
}

export default Cart;