import React from "react";
import ReactDOM from "react-dom";
import { Route, HashRouter } from "react-router-dom";
import Header from "./components/Header";
import Home from "./components/Home";
import Order from "./components/Order";
import Contact from "./components/Contact";
import About from "./components/About";
import Footer from "./components/Footer";
import Cart from "./components/Cart";
import Login from "./components/Login";
import Platform from "./components/Platform";
import { NavLink } from "react-router-dom";
import "./styles/index.css";

import data from './backend.json'

import "./styles/main.css";

class App extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			error: null,
			isLoaded: false,
			items: [],
			cartIsActive: "show-start-order",
			cart: {
				meals: [],
				entrees: [],
				sides: [],
				drinks: [],
				amount: 0
			},
			nextId: 0
		};
	}

	componentWillMount() {
		this.setState({
			isLoaded: true,
			items: data
		});
	}

	componentDidMount() {
		var chuptUrl = "https://chupt-dev-4.appspot.com/pods/DApm5HLNDrE4vpFjanQR65/menu";
		// fetch(chuptUrl)
		// 	.then(res => res.json())
		// 	.then(
		// 		result => {
		// 			this.setState({
		// 				isLoaded: true,
		// 				items: result
		// 			});
		// 		},
		// 		// Note: it's important to handle errors here
		// 		// instead of a catch() block so that we don't swallow
		// 		// exceptions from actual bugs in components.
		// 		error => {
		// 			this.setState({
		// 				isLoaded: true,
		// 				error
		// 			});
		// 		}
		// 	);

		this.handleCartAmount();

		console.log(data)
	}

	handleCartButtons = () => {
		// if (window.location.href.indexOf("cart") > -1) {
		// 	this.setState({ cartIsActive: "show-checkout" });
		// } else {
			this.setState({ cartIsActive: "show-cart" });
		// }
	};

	handleCartAmount = newAmount => {
		// Todo
		// Check out newAmount and see if its necessary

		let newState = this.state;

		let amount = 0;

		newState.cart.meals.forEach(x => (amount += x.count * x.price));

		newState.cart.entrees.forEach(x => (amount += x.count * x.price));

		newState.cart.sides.forEach(x => (amount += x.count * x.price));

		newState.cart.drinks.forEach(x => (amount += x.count * x.price));

		if (
			newState.cart.meals.length > 0 ||
			newState.cart.entrees.length > 0 ||
			newState.cart.sides.length > 0 ||
			newState.cart.drinks.length > 0
		) {
		} else {
			amount = 0;
		}

		newState.cart.amount = amount.toFixed(2);

		this.setState(newState);
	};

	addToCart = cartItem => {
		let cart = this.state.cart;

		switch (cartItem.type) {
			case "meal":
				cart.meals.push({
					id: this.state.nextId,
					item: cartItem,
					count: 1,
					price: 6.5
				});
				this.setState({ nextId: (this.state.nextId += 1) });
				break;
			case "entree":
				cart.entrees.push({
					id: this.state.nextId,
					item: cartItem,
					count: 1,
					price: 5.0
				});
				this.setState({ nextId: (this.state.nextId += 1) });
				break;
			case "side":
				cart.sides.push({
					id: this.state.nextId,
					item: cartItem,
					count: 1,
					price: 3.0
				});
				this.setState({ nextId: (this.state.nextId += 1) });
				break;
			case "drink":
				cart.drinks.push({
					id: this.state.nextId,
					item: cartItem,
					count: 1,
					price: 1.0
				});
				this.setState({ nextId: (this.state.nextId += 1) });
				break;
		}

		this.setState({ cart: cart });

		this.handleCartAmount();
	};

	handleWatermark() {
		console.log('watermark')
		let timerDiv = document.getElementById("timer");
		let original = document.getElementById("watermark")

		original.style.display = "none"

		timerDiv.style.display = "inline-block"
		timerDiv.style.position = "fixed"
		timerDiv.style.top = "0"
		timerDiv.style.width = "auto"
		timerDiv.style.color = "rgb(0, 209, 0);"


		let timer = new Date();
		timer.setHours(0, 5, 0);
		timerDiv.innerHTML = timer.toLocaleTimeString("en-gb")

		function countdown() {
			console.log('function working')
			timer.setSeconds(timer.getSeconds() - 1);
			timerDiv.innerHTML = timer.toLocaleTimeString("en-gb")
			if (timer.getHours() === 0 && timer.getMinutes() === 0 && timer.getSeconds() === 0) {
				clearInterval(myTimer);
				original.style.display = "flex"
				timerDiv.style.display = "none"
			}
		}
		
		let myTimer = setInterval(countdown, 1000);

	}

	render() {
		console.log(this.state)
		return (
			<HashRouter>
				<div id="reactApp" >
					<Header displayCart={this.state.cartIsActive} amount={this.state.cart.amount} />

					<div className="content-wrapper">

						<div className="content">
							{/* <button onClick={() => console.log(this.state)}>Show Global State</button> */}

							<div style={{backgroundColor: ''}} id="watermark">
								<p style={{textAlign: 'center'}}>
									© 2019 Copyright Chupt Corporation
									<br/>
									<a href="https://chupt.com" target="_blank">https://chupt.com</a>
									<br/>
									(Click <a onClick={() => this.handleWatermark()}>here</a> to hide for 5 minutes)
								</p>
							</div>

							<div id="timer"/>


							<Route exact path="/" render={() => <Home handleCartButtons={this.handleCartButtons} />} />
							<Route path="/about" render={() => <About handleCartButtons={this.handleCartButtons} />} />
							<Route path="/contact" component={Contact} />

							<Route path="/platform" component={Platform}  />

							<Route
								path="/menu"
								render={() => (
									<Order
										menu={this.state.items.menu}
										handleCartButtons={this.handleCartButtons}
										addToCart={this.addToCart}
									/>
								)}
							/>

							<Route
								path="/cart"
								render={() => (
									<Cart
										handleCartButtons={this.handleCartButtons}
										cart={this.state.cart}
										handleCartAmount={this.handleCartAmount}
									/>
								)}
							/>
							<Route path="/login" component={Login} />
						</div>
					</div>

					<Footer displayCart={this.state.cartIsActive} amount={this.state.cart.amount} />
				</div>
			</HashRouter>
		);
	}
}

ReactDOM.render(<App />, document.querySelector("#root"));
