import React from 'react';

import Fab from '@material-ui/core/Fab';
import DeleteIcon from '@material-ui/icons/Delete';


class CartItem extends React.Component {

    state = {count: this.props.count}

    handleChange = (math) => {
        let newState = this.state;

        if (math === "+") {
            newState.count++
        } else {
            newState.count--
        }
    
        this.setState(newState)
        this.props.handleCountChange(this.props.id, this.state.count, this.props.item.type)
    }

    handleDisabled = () => {
        if (this.state.count === 1) {
            return true
        } else {
            return false
        }
    }
    
    handleRemove = () => {
        this.props.handleRemove(this.props.id, this.props.item.type)
    }

    capitalizeFirstLetter(string) {
        return string.charAt(0).toUpperCase() + string.slice(1);
    }

    render() {

        let choiceList = [];
        let optionList = [];
        let subSide;

        if (this.props.item.initialSide === this.props.item.currentSide) {
            console.log("side is default")
        }

        console.log(this.props.item)

        if (this.props.item.choices) {
            this.props.item.choices.forEach(
                x => (x.value != x.default ? choiceList.push(<p style={{color: 'red', }}>- {x.name}: {this.capitalizeFirstLetter(x.value)}</p>) : null)
            )
        }

        this.props.item.options.forEach(
            x => (x.value === "True" || x.value === true ? null : optionList.push(<p style={{color: 'red'}}>- No {x.name}</p>))
        )

        return (
            <div className="cart-item-wrapper">

                {/* <button onClick={this.handleRemove}>Remove</button> */}

                <h3 className="cart-item-name">{this.state.count}x {this.props.item.name}</h3>
                
                <div className="cart-item-mods-wrapper">
                    {choiceList}
                    {optionList}
                    {this.props.item.initialSide !== this.props.item.currentSide ? <p style={{color: 'red'}}>Sub Side: {}</p> : null}
                </div>

                <div className="cart-button-wrapper">

                    <Fab
                        color="primary"
                        onClick={this.handleRemove}
                        size="small"
                        className="cart-delete-button"
                    >
                        <img className="cart-remove-item" src={require("../assets/round-close-24px.svg")} />
                    </Fab>

                    <Fab
                        color="primary"
                        onClick={() => this.handleChange("+")}
                        size="small"
                        className="cart-add-button"
                    >
                        <img className="cart-remove-item" src={require("../assets/baseline-add-24px.svg")} />
                    </Fab>

                    <Fab
                        color="primary"
                        disabled={this.handleDisabled()} 
                        onClick={() => this.handleChange("-")}
                        size="small"
                        className="cart-subtract-button"
                    >
                        <img className="cart-remove-item" src={require("../assets/baseline-remove-24px.svg")} />
                    </Fab>

                </div>
                
                

            </div>
        )
    }
}

export default CartItem;