import React from "react";
import OrderNav from "./orders/OrderNav";
import { Button } from "@material-ui/core/";

import { NavLink, BrowserRouter, Route } from "react-router-dom";

import Meals from "./orders/Meals";
import Entrees from "./orders/Entrees";
import Sides from "./orders/Sides";
import Drinks from "./orders/Drinks";
import OrderMods from "./orders/OrderMods";

import OrderList from "./orders/OrderList"

import "../styles/Order.css";

class Order extends React.Component {
	state = { orderPath: "temp" };

	componentWillMount() {
		this.props.handleCartButtons();
	}

	componentDidMount() {
		this.props.handleCartButtons();
		window.scroll(0, 0);
		document.title = "Chupt - Menu"
	}

	handleOrderPath = (orderPath, obj) => {
		this.setState({
			orderPath: orderPath,
			orderMod: obj
		});
	};

	addToCart = cartItem => {
		this.props.addToCart(cartItem);
	};

	render() {
		
		console.log(this.props.menu)

		return (
			<div className="orderWrapper">

				{/* <OrderNav menu={this.props.menu} /> */}

				<div className="order-content">

					{/* Handles second screen for order modes / final add to cart */}
					<Route exact path={`/menu/${this.state.orderPath}`} render={() => <OrderMods menu={this.props.menu} order={this.state.orderMod} addToCart={this.addToCart} />} />

					<Route exact path={'/menu'} render={() => <OrderList menu={this.props.menu} orderPath={this.handleOrderPath} />} />

				</div>
			</div>
		);
	}
}

export default Order;
