import React from 'react'
import { Card, Button, Fab } from "@material-ui/core/";
import { NavLink, Route } from "react-router-dom";

import '../../styles/MenuItem.css'

const style = {
    textAlign: {
        textAlign: 'left',
        margin: '12px 24px 0',
    },
    card: {
		borderRadius: "24px",
        boxShadow: "0 0 0 0",
        marginBottom: '24px'
	},
}

class MenuItem extends React.Component {

    handleToLower = (orderPath) => {
        let newOrderPath = orderPath
            .replace(/ /g, "-")
            .replace(/\(/g, "")
            .replace(/\)/g, "")
            .toLowerCase()
        return newOrderPath;
    }

    handleOrderPath = (newOrderPath, obj) => {
        this.props.orderPath(newOrderPath, obj);
    }

    componentDidMount() {
        window.scroll(0, 0);
    }

    render() {
        
        var imageList = [];

        if (this.props.menuItem.items) {    
            this.props.menuItem.items.map(
                (item) => {return(
                        item.images.map(
                            (itemImage) => {return(    
                                imageList.push(itemImage.image_url)
                            )}
                        )     
                )}
            )
        } else {
            {this.props.menuItem.images.map(
                (itemImage) => {return(
                    imageList.push(itemImage.image_url)
                )}
            )}
        }

        return(
            <div className="menu-card-wrapper">
                    <div className="menu-card-images-wrapper">
                        {imageList.map(
                            (image) => {return(
                                <img style={{maxWidth: 'inherit'}} src={image} />
                            )}
                        )}
                    </div>

                    <div className="menu-item-text-wrapper">
                        <h3 style={style.textAlign} className="text bold">{this.props.menuItem.name}</h3>

                        <p style={style.textAlign} className="menu-item-description text">{this.props.menuItem.description}</p>
                    </div>
                    
                    <div className="menu-item-button">
                        <NavLink 
                            to={`/menu/${this.handleToLower(this.props.menuItem.name)}`} 
                            onClick={() => this.handleOrderPath(this.handleToLower(this.props.menuItem.name), this.props.menuItem)}
                        >
                            <Button 
                                style={{fontFamily: 'Noto Sans' }} 
                                color="primary" 
                                varient="extended"
                            >
                                Choose {this.props.menuItem.item_type}
                            </Button>
                        </NavLink>
                    </div>

                    
                    {/* <p style={style.textAlign} className="menu-item-price">{this.props.amount}</p> */}
            </div>
        )
    }
}

export default MenuItem;