import React from 'react';
// import {Select, InputLabel} from '@material-ui/core';
import Input from '@material-ui/core/Input';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import FilledInput from '@material-ui/core/FilledInput';
import InputLabel from '@material-ui/core/InputLabel';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import NativeSelect from '@material-ui/core/NativeSelect';

class SideSwapper extends React.Component {

    constructor(props) {
        super(props);
        this.state = {value: this.props.menu.sides[0].item_id};
    
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleChange(event) {
        this.setState({value: event.target.value});
    }
    
    handleSubmit(event) {
        
        let sideList = [];

        this.props.menu.sides.map(side => {
            if (side.item_id === this.state.value) {
                return sideList.push(side)
            }
        });

        this.props.changeSide(sideList[0])

        event.preventDefault();
    }

    render() {

        let options = [];
        this.props.menu.sides.map(
            side => {
                return (
                options.push(
                    <option value={side.item_id}>{side.name}</option>
                )
            )}
        )

        const { classes } = this.props;

        return (
            <span className="side-swapper">

                <form onSubmit={this.handleSubmit}>
                    <label>
                    Change Side:
                    <select style={{maxWidth: '300px', marginBottom: "12px"}} value={this.state.value} onChange={this.handleChange}>
                        {options}
                    </select>
                    </label>
                    <input style={{marginBottom: "12px"}} type="submit" value="Submit" />
                </form>

                <button onClick={() => console.log(this.state)}>Show SideSwapper State</button>
                
            </span>
            
            

        )
    }
}

export default SideSwapper;