import React from "react";
import MenuItem from "./MenuItem";
import OrderNav from "./OrderNav";

import "../../styles/OrderList.css"

class OrderList extends React.Component {
	handleOrderPath = (orderPath, obj) => {
		// console.log(orderPath)
		this.props.orderPath(orderPath, obj);
	};

	render() {

		let mealsList = [];
		let entreesList = [];
		let sidesList = [];
		let drinksList = [];

		{
			this.props.menu.meals.map(menuItem => {
				return mealsList.push(
					<MenuItem key={menuItem.item_id} menuItem={menuItem} orderPath={this.handleOrderPath} />
				);
			});
		}
		{
			this.props.menu.entrees.map(menuItem => {
				return entreesList.push(
					<MenuItem key={menuItem.item_id} menuItem={menuItem} orderPath={this.handleOrderPath} />
				);
			});
		}
		{
			this.props.menu.sides.map(menuItem => {
				return sidesList.push(
					<MenuItem key={menuItem.item_id} menuItem={menuItem} orderPath={this.handleOrderPath} />
				);
			});
		}
		{
			this.props.menu.drinks.map(menuItem => {
				return drinksList.push(
					<MenuItem key={menuItem.item_id} menuItem={menuItem} orderPath={this.handleOrderPath} />
				);
			});
		}


		return (
			<div className="category-wrapper">

				<OrderNav menu={this.props.menu} />

				<div className="spacer-md_lg" />
				<span id="order-meals" className="footer-header title-md">
					<p>
						Meals
						<span className="bold salmon"> $6.50</span>
					</p>
				</span>
				<div className="spacer-md_lg" />

				<div className="menu-wrapper">{mealsList}</div>

				<div className="spacer-md_lg" />
				<span id="order-entrees" className="footer-header title-md">
					<p>
						Entreés
						<span className="bold salmon"> $5.00</span>
					</p>
				</span>
				<div className="spacer-md_lg" />

				<div className="menu-wrapper">{entreesList}</div>

				<div className="spacer-md_lg" />
				<span id="order-sides" className="footer-header title-md">
					<p>
						Sides
						<span className="bold salmon"> $3.00</span>
					</p>
				</span>
				<div className="spacer-md_lg" />

				<div className="menu-wrapper">{sidesList}</div>
				<div className="spacer-md_lg" />
				<span id="order-drinks" className="footer-header title-md">
					<p>
						Drinks
						<span className="bold salmon"> $1.00</span>
					</p>
				</span>
				<div className="spacer-md_lg" />

				<div className="menu-wrapper">{drinksList}</div>
			</div>
		);
	}
}

export default OrderList;
