import React from 'react';
import MenuItem from './MenuItem';

import '../../styles/foodCategory.css'

class Meals extends React.Component {

    componentWillMount() {
        this.setState({meals: this.props.meals})
    }

    componentDidMount() {
        window.scroll(0, 0);
        console.log(this.props.meals)
        console.log(this.state.meals)
    }

    handleOrderPath = (orderPath, obj) => {
        // console.log(orderPath)
        this.props.orderPath(orderPath, obj)
    }

    render() {

        return(
            <div className="category-wrapper">
                <h1 className="food-header">Meals</h1>
                {this.state.meals.map(
                    (menuItem) => {
                        return(
                            <MenuItem 
                                key={menuItem.item_id}
                                menuItem={menuItem}
                                orderPath={this.handleOrderPath}
                            />
                        )
                    }
                )}              
            </div>
        )
    }
}

export default Meals;