import React from "react";
import { Card, Button, Fab } from "@material-ui/core/";
import { NavLink, Route } from "react-router-dom";

import OrderModsList from './OrderModsList';

import SideSwapper from '../orders/SideSwapper';
import NativeSelects from '../third-party/material-ui/NativeSelects';

import FormGroup from "@material-ui/core/FormGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';

import '../../styles/Order.css'
import '../../styles/OrderMods.css'

const style = {
    textAlign: {
        textAlign: "left",
        marginLeft: "1.5em"
    },
    card: {
        borderRadius: "24px",
        boxShadow: "0 0 0 0",
        marginBottom: "24px",
        padding: "3px"
    }
};

class OrderMods extends React.Component {

    state = {
        cart: {},
        initialOrder: {}
    };

    componentWillMount() {
        window.scroll(0, 0);
        this.setState({initialOrder: this.props.order})
    }

    handleStateChange = (state) => {
        // console.log(this.state)
        this.setState({cart: state})
    }

    addToCart = (cartItem) => {
        this.props.addToCart(cartItem)
    }

    changeSide = (side) => {
        // console.log(this.state)
        // console.log(side)
        let order = this.state.initialOrder;
        // console.log(order)
        order.items[1] = side;
        this.setState({initialOrder: order})
        // console.log(this.state)
    }

    render() {
        const { classes } = this.props; // Material

        var imageList = [];
        var modList = [];
        var sideSwapper = [];
        var nativeSelects = [];

        if (this.state.initialOrder.items) {
            this.state.initialOrder.items.map(item => {
                return item.images.map(itemImage => {
                    return imageList.push(itemImage.image_url);
                });
            });

            // Handles Mods
            modList.push(
                <OrderModsList 
                    order={this.props.order} 
                    handleStateChange={this.handleStateChange}
                />
            )

            nativeSelects.push(<NativeSelects menu={this.props.menu} changeSide={this.changeSide} />)

        } else {
            {
                this.props.order.images.map(itemImage => {
                    return imageList.push(itemImage.image_url);
                });

                // Handles Mods
                modList.push(
                    <OrderModsList
                        name = {this.props.order.name}
                        id={this.props.order.item_id}
                        mods={this.props.order.mods}
                        type = {this.props.order.item_type}
                        handleStateChange = {this.handleStateChange}
                   />
                )
            }
        }
        
        return (
            <div className="mod-card-wrapper">

                {/* <button onClick={() => console.log(this.state)}>Show OrderMod State</button> */}

                {/* {nativeSelects} */}

                    {/* <h3 style={style.textAlign}>{this.props.order.name}</h3> */}

                    <div className="mod-card-images-wrapper">
                        {imageList.map(
                            (image) => {return(
                                    <img src={image} />
                            )}
                        )}
                    </div>

                    {/* Mod List */}
                    {modList}

                    <span className="add-to-cart-button">
                        <NavLink onClick={() => this.addToCart(this.state.cart)} to={`/menu`} >
                            <Button 
                                style={{fontFamily: 'Lato' }} 
                                color="primary" 
                                varient="extended"
                            >
                                Add To Cart
                            </Button>
                        </NavLink>
                    </span>

                    {/* {sideSwapper} */}

            </div>
        );
    }
}

export default OrderMods;
