import React from "react";
import { Card, Button, Fab } from "@material-ui/core/";
import { NavLink, Route } from "react-router-dom";

import FormGroup from "@material-ui/core/FormGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';

import '../../styles/FoodMods.css'

const style = {
    textAlign: {
        textAlign: "left",
        marginLeft: "1.5em"
    },
    card: {
        borderRadius: "24px",
        boxShadow: "0 0 0 0",
        marginBottom: "24px",
        padding: "3px"
    }
};

class OrderModsList extends React.Component {

    state = {};

    componentWillMount() {
        let choices = [];
        let options = [];
        
        if (this.props.order) {
            // console.log(this.props.order)
            this.props.order.items.map(
                item => { return (
                    item.mods.forEach(x => {
                        switch(x.mod_type) {
                            case 'option':
                                options.push({name: x.name, value: x.value})
                                break
                            case 'choice':
                                const optionList = []
                                x.choices.forEach(y => optionList.push(y.value))
                                choices.push({name: x.name, value: x.value, default: x.default, optionList: optionList})
                        }
                    })
                )}
            )

            let state = {
                name: this.props.order.name, 
                item_id: this.props.order.item_id, 
                choices: choices, 
                options: options, 
                type: this.props.order.item_type, 
                initialSide: this.props.order.items[1].item_id,
                currentSide: this.props.order.items[1].item_id
            }  

            this.setState(state)
            console.log("did mount")

        } else {
            this.props.mods.forEach(x => {
                switch(x.mod_type) {
                    case 'option':
                        options.push({name: x.name, value: x.value})
                        break
                    case 'choice':
                        const optionList = []
                        x.choices.forEach(y => optionList.push(y.value))
                        choices.push({name: x.name, value: x.value, default: x.default, optionList: optionList})
                }
            })  

            let state = {name: this.props.name, item_id: this.props.id, choices: choices, options: options, type: this.props.type}    
            this.setState(state)
           
        }
    }

    componentDidMount() {
        window.scroll(0, 0);
        this.props.handleStateChange(this.state)
    }

    componentWillReceiveProps() {

        let choices = [];
        let options = [];
        
        if (this.props.order) {
            if (this.props.order.items[1].item_id !== this.state.currentSide) {
                this.props.order.items.map(
                    item => { return (
                        item.mods.forEach(x => {
                            switch(x.mod_type) {
                                case 'option':
                                    options.push({name: x.name, value: x.value})
                                    break
                                case 'choice':
                                    const optionList = []
                                    x.choices.forEach(y => optionList.push(y.value))
                                    choices.push({name: x.name, value: x.value, default: x.default, optionList: optionList})
                            }
                        })
                    )}
                )
    
                let state = {
                    name: this.props.order.name, 
                    item_id: this.props.order.item_id, 
                    choices: choices, 
                    options: options, 
                    type: this.props.order.item_type,
                    currentSide: this.props.order.items[1].item_id
                }  
    
                this.setState(state)
            }
        }
            
        
    }

    handleStateChange(state) {
        this.props.handleStateChange(state)
    }

    handleChoiceChange = name => event => {
        let newChoiceState = this.state.choices;

        newChoiceState.forEach(
            x => {
                if (x.name === name) {
                    x.value = event.target.value
                }
            }
        )
            
        this.setState({choices: newChoiceState})  
        this.handleStateChange(this.state)
    }

    handleOptionChange = name => event => {
        let newOptionState = this.state.options;

        newOptionState.forEach(
            x => {
                if (x.name === name) {
                    x.value = !x.value
                }
            }
        )    

        this.setState({options: newOptionState})  
        this.handleStateChange(this.state)
    }

    capitalizeFirstLetter(string) {
        return string.charAt(0).toUpperCase() + string.slice(1);
    }

    render() {

        if (this.state.choices) {
        return (
            <div className="food-mod-wrapper">

                <h3 className="title-md">Mods for {this.state.name}</h3>
                
                <div className="mod-form-group">
                    <FormGroup>

                        {/* Choices */}
                        {this.state.choices.map(
                            choice => { return (
                                <span>
                                    <h4 className="title-sm" >Choose One</h4>
                                    <RadioGroup name={choice.name} onChange={this.handleChoiceChange(choice.name)} defaultValue={choice.value}>
                                        {choice.optionList.map(
                                            option => {
                                                return (
                                                    <FormControlLabel 
                                                        value={option} 
                                                        control={<Radio />} 
                                                        label={this.capitalizeFirstLetter(option)} 
                                                        labelPlacement="end"
                                                    />
                                                )
                                            }
                                        )}
                                    </RadioGroup>
                                </span>
                            )}
                        )}

                        {/* Options */}
                        <h4 className="title-sm" >Choose Options</h4>
                        {this.state.options.map(
                            option => {
                                // console.log(option)
                                return (
                                    <span>
                                        <FormControlLabel
                                            control={
                                                <Checkbox
                                                    checked={option.value}
                                                    onClick={this.handleOptionChange(option.name)}
                                                    value={option.value}
                                                />
                                            }
                                            label={option.name}
                                        /> 
                                    </span>
                                )
                            }
                        )}

                    </FormGroup>
                </div>
            </div>
        );} else { return null }
    }
}

export default OrderModsList;
