import React from 'react';
import MenuItem from './MenuItem';
import '../../styles/foodCategory.css'

class Entrees extends React.Component {
    componentDidMount() {
        // console.log(this.props.meals[0].description)
    }

    handleOrderPath = (orderPath, obj) => {
        // console.log(orderPath)
        this.props.orderPath(orderPath, obj)
    }

    render() {
        return(
            <div className="category-wrapper">
                <h1 className="food-header">Entrees</h1>
                {this.props.entrees.map(
                    (menuItem) => {
                        return(
                            <MenuItem 
                                key={menuItem.item_id}
                                menuItem={menuItem}
                                orderPath={this.handleOrderPath}
                            />
                        )
                    }
                )}  
            </div>
        )
    }
}

export default Entrees;